@use "./variables.scss" as *;

.button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: $font-secondary;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 143%;
  text-align: center;
  text-transform: uppercase;
  margin: 0 auto;
  color: $white-color;
  min-height: 40px;
  background: $main-color;
  border: none;
  width: 100%;

  &:hover {
    background-color: $green-special;
  }

  &:active {
    background-color: $green-special;
    color: $white-color-button;
  }
}
